            <li> En 2017 se celebró el 16 de octubre con el lema «Mujeres, saber y poder» inspirándose en el término empoderamiento, que define el proceso seguido para aumentar la fortaleza de individuos y comunidades en relación a su situación de pobreza o marginación. Los textos han sido seleccionados por Ana Caballé profesora de literatura española y responsable de la Unidad de Estudios Biográficos de la Universidad de Barcelona. Las autoras centrales de la conmemoración son: Teresa de Jesús, Gertrudis Gómez de Avellaneda, Carolina Coronado, Mercedes Cabello de Carbonera, Emilia Pardo Bazán, Victor Català (Caterina Albert), Juana de Ibarbourou, Elena Fortún, Teresa de la Parra, Maria Etxabe, Julia de Burgos, Alfonsina Storni, Dolores Medio, Rosario Castellanos, Mercè Rodoreda, Elena Soriano, Elena Garro, Begoña Caamaño, Carmen Martín Gaite y Gloria Fuertes.</li>

