            <div>

                <li>En 2019 El día de las Escritoras se adentra en el tema del amor, desde la perspectiva de las mujeres, a lo largo de cinco siglos. El intento es más original de lo que parece. Si consideramos la escasa libertad intelectual que ha tenido la mujer en la Historia, resulta inevitable deducir que el relato del amor, que atraviesa nuestra cultura, es una construcción masculina. La relación de las mujeres con el amor ha sido de gran intensidad, qué duda cabe. Pero la autoría del relato no es suya. Así, las voces de estas diecinueve escritoras españolas y latinoamericanas quizás nos ofrezcan una visión particular sobre un territorio en el que, en cierto modo, son extranjeras. O lo fueron.

                    Muchas mujeres pueden no sentirse reflejadas con este planteamiento. Y tendrán razón, porque ¿a qué llamamos exactamente amor? ¿Existe palabra más subjetiva y polisémica? ¿Y qué relación tiene con la palabra libertad, que también aparece en el título de esta antología?

                    El universo femenino ha girado alrededor del amor desde la noche de los tiempos. Excluidas del ámbito del conocimiento y el desarrollo profesional, relegadas a la dependencia del hombre, es posible que el amor haya sido, demasiadas veces, la única fuente de riqueza de las mujeres. Una posibilidad de realización personal en su condición casi exclusiva de amantes, madres y cuidadoras. Una importante fuente de felicidad.</li>

                <li>
